function BoothToMF() {
    // 
    const [taxSales, setTaxSales] =  useLocalStorage('tax-sales', '');
    const [taxCommission, setTaxCommission] = useLocalStorage('tax-commission', '');
    const [saleSbj2, setSaleSbj2] = useLocalStorage('sales-subject', '');
    const [receivableSbj2, setReceivableSbj2] = useLocalStorage('receivable-subject', '');
    const handleChangeTaxSales = (event: SelectChangeEvent) => {
        setTaxSales(event.target.value);
    };
    const handleChangeTaxCommission = (event: SelectChangeEvent) => {
        setTaxCommission(event.target.value);
    };
    const handleChangeSaleSbj2 = (event: any) => {
        console.log(event)
        setSaleSbj2(event.target.value);
    };
    const handleChangeReceivableSbj2 = (event: any) => {
        setReceivableSbj2(event.target.value);
    };

    // 
    const [fileOrigin, setFileOrigin] = React.useState<File>();
    const [fileOriginName, setFileOriginName] = React.useState('');
    const onChangeFileOrigin = (event: React.FormEvent) => {
        const files = (event.target as HTMLInputElement).files
        if (files) {
            setFileOrigin(files[0]);
            setFileOriginName(files[0]['name']);
        }
    };

    // 
    const rows00 = [
        { id: 0, no: null, firstName: null, age: null }
    ]
    const [rows, setRows] = React.useState<Array<{ [key: string]: any; }>>(rows00);
    function handleSubmit() {
        if(fileOrigin) {
            console.log('origin: ', fileOriginName);
            const fileReader = new FileReader();
            fileReader.onload = (e2) => {
                if (e2 && e2.target && e2.target.result) {
                    const { readString } = usePapaParse();
                    readString(e2.target.result.toString(), {
                        worker: true,
                        header: true,
                        complete: (results) => {
                            setRows(transformCsv(results['data'], {
                                taxSales: taxSales,
                                taxCommission: taxCommission,
                                saleSbj2: saleSbj2,
                                receivableSbj2: receivableSbj2,
                            }))
                        },
                    });
                }
            }
            fileReader.readAsText(fileOrigin);
        }
    }

    return (
        <div>
            <Box sx={{ my: 2 }}>
                <Typography></Typography>
                <Typography variant="caption" color="text.secondary">
                    
                </Typography>
                <Grid container spacing={3}>
                    <Grid item xs={3}>
                        <FormControl sx={{ my: 2 }}>
                            <InputLabel id="tax-sales-select-autowidth-label">()</InputLabel>
                            <Select
                                labelId="tax-sales-select-autowidth-label"
                                id="tax-sales"
                                value={taxSales}
                                onChange={handleChangeTaxSales}
                                label="()"
                            >
                                <MenuItem value=""></MenuItem>
                                <MenuItem value=" 10%"> 10%</MenuItem>
                                <MenuItem value=" 10% "> 10% </MenuItem>
                                <MenuItem value=" 10% "> 10% </MenuItem>
                                <MenuItem value=" 10% "> 10% </MenuItem>
                                <MenuItem value=" 10% "> 10% </MenuItem>
                                <MenuItem value=" 10% "> 10% </MenuItem>
                                <MenuItem value=" 10% "> 10% </MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={9}>
                        <FormControl sx={{ my: 2 }}>
                            <InputLabel id="tax-commission-select-autowidth-label">()</InputLabel>
                            <Select
                                labelId="tax-commission-select-autowidth-label"
                                id="tax-commission"
                                value={taxCommission}
                                onChange={handleChangeTaxCommission}
                                label="()"
                            >
                                <MenuItem value=""></MenuItem>
                                <MenuItem value=" 10%"> 10%</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                </Grid>
            </Box>

            <Box sx={{ my: 2 }}>
                <Typography></Typography>
                <Typography variant="caption" color="text.secondary">
                    Booth
                </Typography>
                <Grid container spacing={3}>
                    <Grid item xs={3} sx={{ my: 2 }}>
                        <TextField id="sales-sbj2" label="" value={saleSbj2} onChange={handleChangeSaleSbj2}/>
                    </Grid>
                    <Grid item xs={9} sx={{ my: 2 }}>
                        <TextField id="receivable-sbj2" label="" value={receivableSbj2} onChange={handleChangeReceivableSbj2} />
                    </Grid>
                </Grid>
            </Box>

            <Box sx={{ my: 2 }}>
                <Typography></Typography>
                <Typography variant="caption" color="text.secondary">
                    BoothCSV <a href="https://manage.booth.pm/sales/csv_requests" target="_blank">CSV | Booth</a> 
                </Typography>
                <Grid container spacing={3}>
                    <Grid item xs={3} sx={{ my: 2 }}>
                        <Button
                            component="label"
                            variant="outlined"
                        >
                            CSV
                            <input
                                type="file"
                                accept="text/csv"
                                hidden
                                onChange={onChangeFileOrigin}
                            />
                        </Button>
                    </Grid>
                    <Grid item xs={9} sx={{ my: 2 }}>
                        <TextField
                            id="standard3-basic"
                            label="CSV"
                            value={fileOriginName}
                            variant="filled"
                            InputProps={{
                                readOnly: true,
                            }}
                        />
                    </Grid>
                </Grid>
            </Box>

            <Box sx={{ my: 2 }}>
                <Typography></Typography>
                <Typography variant="caption" color="text.secondary">
                    MoneyForwardEXPORTCSV
                </Typography>
                <Grid container spacing={3}>
                    <Grid item xs={12} sx={{ my: 2 }}>
                        <Button variant="outlined" onClick={handleSubmit}></Button>
                    </Grid>
                </Grid>
            </Box>

            <Box sx={{ my: 4 }}>
                <div style={{ height: 400, width: '100%' }}>
                    <DataGrid
                        rows={rows}
                        columns={columns}
                        disableSelectionOnClick
                        components={{
                            Toolbar: CustomToolbar,
                        }}
                    />
                </div>
            </Box>

            <Box sx={{ my: 2 }}>
                <Typography>MoneyForward</Typography>
                <Typography variant="caption" color="text.secondary">
                    MoneyForward:    :  (CSV) 
                </Typography>
            </Box>

        </div>
    )
}

